'use client';

import Image from 'next/image';
import { useState } from 'react';

// 範例資料
import data from './_data/books.json';

// 實心圖
import bookmarkIconFill from './_icons/bookmark-fill.svg';
// 空心圖
import bookmarkIcon from './_icons/bookmark.svg';

export default function BookListPage() {
  // 將導入的書籍資料擴充，多一個屬性 bookmarked (布林值，預設false)代表是否有加入收藏
  const initState = data.map((v) => {
    return { ...v, bookmarked: false };
  });

  // 宣告書籍的狀態
  const [books, setBooks] = useState(initState);

  // 處理書籍收藏狀態切換
  const onToggleBookmarked = (bookIsbn: string) => {
    const nextBooks = books.map((v) => {
      // v 是每本書的資料物件了
      if (v.isbn === bookIsbn) {
        return { ...v, bookmarked: !v.bookmarked };
      } else {
        return v;
      }
    });

    setBooks(nextBooks);
  };

  return (
    <>
      <h1>書籍清單</h1>
      <table>
        <thead>
          <tr>
            <th>ISBN</th>
            <th>標題</th>
            <th>作者</th>
            <th>加入收藏</th>
          </tr>
        </thead>
        <tbody>
          {books.map((book) => {
            return (
              <tr key={book.isbn}>
                <td>{book.isbn}</td>
                <td>{book.title}</td>
                <td>{book.author}</td>
                <td>
                  <Image
                    onClick={() => {
                      onToggleBookmarked(book.isbn);
                    }}
                    // 依據bookmarked的值來呈現實心(true)或空心(false)的圖
                    src={book.bookmarked ? bookmarkIconFill : bookmarkIcon}
                    alt=""
                  />
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </>
  );
}
